<template>
    <video-player class="vjs-custom-skin" ref="videoPlayer" :options="playerOptions" :playsinline="true"
        customEventName="customstatechangedeventname" @play="onPlayerPlay($event)" @pause="onPlayerPause($event)"
        @ended="onPlayerEnded($event)" @waiting="onPlayerWaiting($event)" @playing="onPlayerPlaying($event)"
        @loadeddata="onPlayerLoadeddata($event)" @timeupdate="onPlayerTimeupdate($event)" @canplay="onPlayerCanplay($event)"
        @canplaythrough="onPlayerCanplaythrough($event)" @statechanged="playerStateChanged($event)" @ready="playerReadied">
    </video-player>
</template>


<script>
    //
    // video-player-box
    // Similarly, you can also introduce the plugin resource pack you want to use within the component
    // import 'some-videojs-plugin'
    export default {
        name: "Video",
        props: {
            src: String,
            poster: String,
            onNext:Function
        },
        data() {
            return {
                playerOptions: {
                    playbackRates: [0.7, 1.0, 1.5, 2.0], //播放速度
                    autoplay: true, //如果true,浏览器准备好时开始回放。
                    controls: true, //控制条
                    preload: 'auto', //视频预加载
                    muted: false, //默认情况下将会消除任何音频。
                    loop: false, //导致视频一结束就重新开始。
                    language: 'zh-CN',
                    aspectRatio: '16:9', // 将播放器置于流畅模式，并在计算播放器的动态大小时使用该值。值应该代表一个比例 - 用冒号分隔的两个数字（例如"16:9"或"4:3"）
                    fluid: true, // 当true时，Video.js player将拥有流体大小。换句话说，它将按比例缩放以适应其容器。
                    sources: [{
                        type: 'application/x-mpegURL',
                        src: this.$props.src
                        //src: "https://cdn.letv-cdn.com/2018/12/05/JOCeEEUuoteFrjCg/playlist.m3u8"
                    }],
                    poster: this.$props.poster, //你的封面地址
                    width: document.documentElement.clientWidth,
                    notSupportedMessage: '此视频暂无法播放，请稍后再试' //允许覆盖Video.js无法播放媒体源时显示的默认信息。
                }
            }
        },
        mounted() {
            window.console.log('this is current player instance object', this.player)
        },
        computed: {
            player() {
                return this.$refs.videoPlayer.player
               
            }
            
        },
        methods: {
            onChangePlaySource(url){
                this.playerOptions.sources[0].src = url
                //this.$refs.videoPlayer.player.play()
                
            },
            // listen event
            onPlayerPlay(player) {

                // console.log('player play!', player)
            },
            onPlayerPause(player) {
                // console.log('player pause!', player)
            },
            // ...player event
            onPlayerLoadeddata(e){
                
            },
            onPlayerEnded(e){
                this.onNext()
            },
            onPlayerWaiting(e){},
            onPlayerPlaying(e){},
            onPlayerTimeupdate(e){},
            onPlayerCanplay(e){},
            onPlayerCanplaythrough(e){},
            // or listen state event
            playerStateChanged(playerCurrentState) {
                // console.log('player current update state', playerCurrentState)
            },
            

            // player is ready
            playerReadied(player) {
                window.console.log('the player is readied', player)
                // you can use it to do something...
                // player.[methods]
                //player.play()
            }
        }
    }
</script>
