                    this.ep -= 1
                    this.ep = this.ep % (this.episodes.length + 1)
                    if (this.ep == 0) {
                        this.ep = this.episodes.length
                    }
                    this.onChange(this.ep)
                } else if (val == "forward") {
                    this.ep += 1
                    this.ep = this.ep % (this.episodes.length + 1)
                    if (this.ep == 0) {
